html {
  background: url(/pastforward/static/media/bg.4dfc9091.jpg);
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

video {
  position: fixed;
  top: 15%;
  left: 0;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: 100%;
  z-index: 9999;
}

@font-face { font-family: 'GermaniaVersalien'; src: url(/pastforward/static/media/GermaniaVersalien.d36b44b6.ttf) format('truetype'); }
@font-face { font-family: 'DistortionDosAnalogue'; src: url(/pastforward/static/media/DistortionDosAnalogue.8faf7581.otf) format('truetype'); }
.App-header {
  margin-left: 5vh;
}

.App-Link {
  text-decoration: none;
  color:white;
}

.App-past {
  font-size: calc(48px + 2vmin);
  font-family: GermaniaVersalien, arial, sans-serif;
  margin-right: 1vh;
}

.App-forward {
  font-size: calc(48px + 2vmin);
  font-family: DistortionDosAnalogue, arial, sans-serif;
}

@font-face { font-family: 'ModernGermanHandwriting'; src: url(/pastforward/static/media/ModernGermanHandwriting.535afad2.ttf) format('truetype'); }

img {
  display: block;
  position: absolute;
  z-index: 1;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
  top: 0px;
  left: 0px;
  /* though this won't be flush, due to transform (actual top and left = half the width/height of image */
  border: 20px transparent solid;
  background-color: #eee;
  background-origin: border-box;
  background-repeat: no-repeat;
  background-position: center bottom, center top, left center, right center, 0 15%, 0 85%, 100% 15%, 100% 85%, 15% 0, 85% 0, 15% 100%, 85% 100%, 100% 70%;
  margin: 0;
  padding: 0;
}

.Image-text {
  display: block;
  position: absolute;
  color: black;
  font-family: ModernGermanHandwriting, arial, sans-serif;
  font-size: calc(36px + 2vmin);
  margin: 25vh;
}
